"use client";

import { useState } from "react";

export default function FlipCard({ front, back, bg, Icon }) {
  const [flipped, setFlipped] = useState(false);

  return (
    <div
      className="
        relative w-full h-56 md:h-64 
        cursor-pointer 
        transition-all duration-500 
      "
      style={{ borderRadius: "1.5rem" }}
      onMouseEnter={() => setFlipped(true)}
      onMouseLeave={() => setFlipped(false)}
    >
      {/* MAIN TILT WRAPPER */}
      <div
        className={`
          absolute inset-0 rounded-3xl border border-border p-6 
          transition-all duration-500 ease-out
          bg-card
          ${flipped ? "shadow-[0_8px_30px_rgba(0,0,0,0.08)] scale-[1.03]" :
                       "shadow-[0_4px_15px_rgba(0,0,0,0.04)] scale-[1.00]"}
        `}
        style={{
          backgroundColor: bg,
          transform:
            flipped
              ? "rotateY(12deg) rotateX(4deg)"
              : "rotateY(0deg) rotateX(0deg)",
          transformOrigin: "center",
        }}
      >
        {/* FRONT CONTENT */}
        <div
          className={`
            absolute inset-0 flex flex-col items-center justify-center gap-4
            transition-all duration-500
            ${flipped ? "opacity-0 rotate-y-12 translate-y-2" : "opacity-100 rotate-y-0"}
          `}
        >
          <div
            className={`
              p-4 rounded-xl bg-primary/15 text-primary 
              transition-all duration-500
              ${flipped ? "translate-y-2 opacity-0" : "translate-y-0 opacity-100"}
            `}
          >
            {Icon && <Icon size={36} />}
          </div>

          <div className="text-center">
            <div className="text-3xl font-bold text-foreground">{front.number}</div>
            <div className="text-sm text-muted-foreground">{front.label}</div>
          </div>
        </div>

        {/* BACK CONTENT */}
        <div
          className={`
            absolute inset-0 flex items-center justify-center text-center px-4
            transition-all duration-500
            ${flipped ? "opacity-100 rotate-y-0 translate-y-0" : "opacity-0 -rotate-y-12 -translate-y-2"}
          `}
        >
          <p className="text-sm leading-relaxed text-foreground/85">
            {back.desc}
          </p>
        </div>
      </div>
    </div>
  );
}
